import { EVALUATION_CONSTANTS, PROBLEM_CONSTANTS, REVIEW_WEIGHTS, REVIEW_INTERVALS, PRIORITY_CONSTANTS } from '../../constants';
import { LoggerFactory } from '../../utils/logger';
const logger = LoggerFactory.getInstance().getLogger(['Records', 'ReviewManager']);
export class ReviewManager {
    /**
     *  session 
     * @returns 
     */
    static evaluateSession(problemDetail, session) {
        // 
        const totalSubmissions = session.submissions.length;
        if (totalSubmissions === 0)
            return false;
        const successfulSubmissions = session.submissions.filter(s => s.status === 'success').length;
        const successRate = successfulSubmissions / totalSubmissions;
        // 
        const isSuccessful = session.firstSuccessTime !== undefined;
        // 
        // endTime
        if (!session.endTime)
            return false;
        const solvingTime = session.endTime - session.startTime;
        const difficulty = problemDetail.difficulty;
        const expectedTime = PROBLEM_CONSTANTS.EXPECTED_SOLVING_TIME[difficulty].expected;
        const isTimeWithinExpected = solvingTime <= expectedTime;
        // 
        const isAccRateGood = successRate >= EVALUATION_CONSTANTS.RECENT_ACC_THRESHOLD;
        // 
        const result = isSuccessful && isTimeWithinExpected && isAccRateGood;
        if (!result) {
            logger.debug('Session evaluated as not mastered', {
                problemId: problemDetail.id,
                sessionId: session.sessionId,
                details: { isSuccessful, isTimeWithinExpected, isAccRateGood }
            });
        }
        return result;
    }
    /**
     * 
     * @returns 
     */
    static evaluateSessions(sessions) {
        if (sessions.length < EVALUATION_CONSTANTS.HISTORY_SESSION_COUNT) {
            logger.debug('Not enough history sessions', {
                problemId: sessions[0].problemId,
                sessionCount: sessions.length
            });
            return false;
        }
        // 
        const sessionAccRates = sessions.map(session => {
            const total = session.submissions.length;
            if (total === 0)
                return 0;
            const successful = session.submissions.filter(s => s.status === 'success').length;
            return successful / total;
        });
        const avgAccRate = sessionAccRates.reduce((a, b) => a + b, 0) / sessionAccRates.length;
        if (avgAccRate < EVALUATION_CONSTANTS.HISTORY_ACC_THRESHOLD) {
            logger.debug('History success rate too low', {
                problemId: sessions[0].problemId,
                avgAccRate: avgAccRate
            });
            return false;
        }
        // 
        const firstSolveTimes = sessions
            .filter(s => s.firstSuccessTime)
            .map(s => s.firstSuccessTime - s.startTime);
        if (firstSolveTimes.length === 0) {
            logger.debug('No first solve times', {
                problemId: sessions[0].problemId
            });
            return false;
        }
        const avgFirstSolveTime = firstSolveTimes.reduce((a, b) => a + b, 0) / firstSolveTimes.length;
        const avgFirstSolveMinutes = avgFirstSolveTime / (60 * 1000);
        // :
        // 1. session
        // 2. 
        // 3. 
        return avgFirstSolveMinutes <= EVALUATION_CONSTANTS.HISTORY_FIRST_SOLVE_MINUTES;
    }
    /**  */
    static isProblemMastered(sessions, problemDetail, now) {
        if (sessions.length === 0) {
            logger.debug('No sessions for problem', {
                problemId: problemDetail.id
            });
            return false;
        }
        // 
        const sortedSessions = [...sessions].sort((a, b) => b.startTime - a.startTime);
        const latestSession = sortedSessions[0];
        //  session 15
        const daysDiff = (now - latestSession.startTime) / (24 * 60 * 60 * 1000);
        if (daysDiff <= EVALUATION_CONSTANTS.RECENT_SESSION_DAYS) {
            //  session 
            if (this.evaluateSession(problemDetail, latestSession)) {
                return true;
            }
        }
        // 
        return this.evaluateSessions(sessions);
    }
    /**
     * 
     * @returns 0-1
     */
    static calculateTimeScore(hoursSinceLastAttempt, reviewIntervals) {
        let timeScore = 0;
        // 
        const lastReviewHour = reviewIntervals.filter(h => h < hoursSinceLastAttempt).pop();
        const nextReviewHour = reviewIntervals.find(h => h > hoursSinceLastAttempt);
        if (lastReviewHour) {
            // 
            const hoursFromLastPoint = hoursSinceLastAttempt - lastReviewHour;
            // 
            const toleranceHours = lastReviewHour < 24
                ? 2 // 242
                : lastReviewHour < 72
                    ? 6 // 1-36
                    : 12; // 312
            if (hoursFromLastPoint <= toleranceHours) {
                // 
                const missedScore = 0.8 * (1 - hoursFromLastPoint / toleranceHours);
                timeScore = Math.max(timeScore, missedScore);
            }
        }
        if (nextReviewHour) {
            // 
            const hoursUntilNextPoint = nextReviewHour - hoursSinceLastAttempt;
            // 
            const advanceHours = nextReviewHour < 24
                ? 1 // 241
                : nextReviewHour < 72
                    ? 3 // 1-33
                    : 6; // 36
            if (hoursUntilNextPoint <= advanceHours) {
                // 
                const approachingScore = 0.6 * (1 - hoursUntilNextPoint / advanceHours);
                timeScore = Math.max(timeScore, approachingScore);
            }
        }
        return timeScore;
    }
    static calculatePriorityScore(problemSessions, problemDetail, now) {
        const latestSession = [...problemSessions].sort((a, b) => b.startTime - a.startTime)[0];
        const hoursSinceLastAttempt = (now - latestSession.startTime) / (60 * 60 * 1000);
        // 1. 
        const reviewHours = Object.values(REVIEW_INTERVALS);
        const timeScore = this.calculateTimeScore(hoursSinceLastAttempt, reviewHours);
        // 2. 
        const difficulty = problemDetail.difficulty;
        const valueScore = PROBLEM_CONSTANTS.DIFFICULTY_DISTRIBUTION[difficulty];
        // 3. 
        const weights = REVIEW_WEIGHTS[difficulty];
        // 3.1 
        const practiceCount = problemSessions.length;
        const normalizedPracticeCount = Math.min(practiceCount / PRIORITY_CONSTANTS.PERFORMANCE.PRACTICE_COUNT_BASE, 1);
        // 3.2 
        const sortedSessions = [...problemSessions].sort((a, b) => b.startTime - a.startTime);
        // 
        const recentWindowMs = PRIORITY_CONSTANTS.PERFORMANCE.RECENT_WINDOW_DAYS * 24 * 60 * 60 * 1000;
        const recentSessions = sortedSessions.filter(s => now - s.startTime <= recentWindowMs);
        const historySessions = sortedSessions.filter(s => now - s.startTime > recentWindowMs);
        // 
        const hasEnoughRecentPractice = recentSessions.length >= PRIORITY_CONSTANTS.PERFORMANCE.MIN_SESSIONS_FOR_TREND;
        // 
        const getSuccessRate = (sessions) => {
            if (sessions.length === 0)
                return 0;
            return sessions.reduce((sum, session) => {
                const sessionSuccessRate = session.submissions.filter(s => s.status === 'success').length /
                    Math.max(session.submissions.length, 1);
                return sum + sessionSuccessRate;
            }, 0) / sessions.length;
        };
        const recentSuccessRate = getSuccessRate(recentSessions);
        const historySuccessRate = hasEnoughRecentPractice ? getSuccessRate(historySessions) : recentSuccessRate;
        // 
        const performanceTrend = hasEnoughRecentPractice && historySuccessRate > recentSuccessRate
            ? (historySuccessRate - recentSuccessRate)
            : 0;
        // 
        const performanceScore = (1 - recentSuccessRate) * weights.failureRate +
            performanceTrend * weights.solvingTime +
            normalizedPracticeCount * weights.timeDeviation;
        // 
        logger.debug('Calculating priority score', {
            problemId: problemDetail.id,
            scores: {
                time: timeScore,
                value: valueScore,
                performance: performanceScore
            }
        });
        const finalScore = timeScore * PRIORITY_CONSTANTS.WEIGHTS.TIME_SCORE +
            valueScore * PRIORITY_CONSTANTS.WEIGHTS.VALUE_SCORE +
            performanceScore * PRIORITY_CONSTANTS.WEIGHTS.PERFORMANCE_SCORE;
        return Math.min(finalScore, 1);
    }
    /**
     * ID
     * @returns ID
     */
    static getReviewSuggestions(sessions, problems, now) {
        // IDsessions
        const sessionsByProblem = new Map();
        sessions.forEach(session => {
            var _a;
            const problemId = session.problemId;
            if (!sessionsByProblem.has(problemId)) {
                sessionsByProblem.set(problemId, []);
            }
            (_a = sessionsByProblem.get(problemId)) === null || _a === void 0 ? void 0 : _a.push(session);
        });
        // 
        const problemScores = [];
        sessionsByProblem.forEach((problemSessions, problemId) => {
            const problemDetail = problems[problemId];
            if (!problemDetail)
                return;
            // 
            if (this.isProblemMastered(problemSessions, problemDetail, now)) {
                return;
            }
            const priority = this.calculatePriorityScore(problemSessions, problemDetail, now);
            problemScores.push({ id: problemId, priority });
        });
        // 
        const sortedProblems = problemScores
            .sort((a, b) => b.priority - a.priority)
            .map(p => p.id);
        logger.info('Review suggestions', {
            problemCount: sortedProblems.length,
            problems: problemScores.map(p => ({
                id: p.id,
                priority: p.priority.toFixed(3)
            }))
        });
        return sortedProblems;
    }
}
